import React                                                from 'react';
import PropTypes                                            from 'prop-types';


import TextField                                            from 'material-ui/TextField';
import { RadioButton, RadioButtonGroup }                    from 'material-ui/RadioButton';
import { Tabs , Tab }                                       from 'material-ui/Tabs';
import SwipeableViews                                       from 'react-swipeable-views';
import RaisedButton                                         from 'material-ui/RaisedButton';




import { ObjectUtil }		                                from '../../../../../_util/util.object';
import { ConstantServicePackage }                           from '../../../../../_constant/constant.servicePackage';


import ComponentAbstract                                    from '../../../../_abstract/c.abstract';
import ComponentOnBoardStep2Form                            from './ch.onboardSubmit.detail.step.2.form';



import  './s.onboardSubmit.detail.step.2.css';









export default class ComponentOnBoardStep2 extends ComponentAbstract {


        static propTypes = {
            data                : PropTypes.object
            , form              : PropTypes.object
            , result            : PropTypes.object

            , formValidation    : PropTypes.array

            , onChangeForm      : PropTypes.func

            , onClickNext       : PropTypes.func
            , onClickPrev       : PropTypes.func
        }


        static defaultProps = {
            data                : {}
            , form              : {}
            , result            : {}

            , formValidation    : [ ]

            , onChangeForm      : () => {}

            , onClickNext       : () => {}
            , onClickPrev       : () => {}
        }






        constructor( props ) {
            super( props );


            this.state = {
                slideIndex              : 0


                , isInquiry             : false
                , isSoleProprietor      : false
                , isPartnership         : false
                , isCustomize           : false
            }
        }






        _onReady() {

            let isInquiry = false;
            let isSoleProprietor = false;
            let isPartnership = false;
            let isCustomize = false;

            for ( let i = 0 ; i < this.props.data.servicePackage.length; i ++ ) {
                let tmpServicePackage = this.props.data.servicePackage[ i ];
                if ( this.props.result.spCode == tmpServicePackage[ 'spCode' ] ) {
                    if ( tmpServicePackage[ 'spType' ] == ConstantServicePackage.TYPE.INQUIRY ) {
                        isInquiry = true;

                    } else if ( tmpServicePackage[ 'spType' ] == ConstantServicePackage.TYPE.SOLE_PROPRIETOR ) {
                        isSoleProprietor = true;

                    } else if ( tmpServicePackage[ 'spType' ] == ConstantServicePackage.TYPE.PARTNERSHIP ) {
                        isPartnership = true;

                    } else if ( tmpServicePackage[ 'spType' ] == ConstantServicePackage.TYPE.CUSTOMIZED ) {
                        isCustomize =  true;

                    }
                }
            }

            this.setState( {
                isInquiry : isInquiry
                , isSoleProprietor : isSoleProprietor
                , isPartnership : isPartnership
                , isCustomize : isCustomize
            } );


        }






        render() {

            return (

                <div>

                    <h5
                            style={ {
                                marginBottom    : 0
                                , marginTop     : 15
                                , marginBottom  : 30
                            } }>
                        Creation User
                    </h5>

                    <hr />


                    <Tabs
                            onChange={
                                ( value ) => {
                                    this.setState( { slideIndex : value } );
                                } }
                            value={ this.state.slideIndex }
                            style={ { backgroundColor : 'transparent' } }>


                        <Tab    value={ 0 } label={ "Mandantory 1" } ></Tab>

                        {
                            ( () => {

                                if ( this.state.isPartnership ) {
                                    return (
                                        <Tab    value={ 1 } label={ "Mandantory 2" } ></Tab>
                                    );

                                } else {
                                    return ( null );

                                }

                            } )()
                        }

                        {
                            ( () => {

                                if ( this.state.isInquiry || this.state.isPartnership ) {
                                    return (
                                        <Tab    value={ 2 } label={ "Optional" } ></Tab>
                                    );

                                } else {
                                    return ( null );

                                }

                            } )()
                        }

                    </Tabs>

                    <SwipeableViews
                                        index={ this.state.slideIndex }
                                        onChangeIndex={
                                            ( value ) => {
                                                this.setState( { slideIndex : value } );
                                            }
                                        } >


                        <div>
                            <ComponentOnBoardStep2Form
                                    showTokenType={ this.state.isSoleProprietor == true || this.state.isPartnership == true }
                                    showTransactionMode={ this.state.isPartnership == true || this.state.sCustomize == true }

                                    data={ this.props.data }
                                    form={ this.props.form[ 'formUser1' ] }
                                    result={ this.props.result }

                                    formValidation={
                                        this.props.formValidation.length > 0
                                        ? this.props.formValidation[ 0 ]
                                        : {}
                                    }

                                    onChangeForm={ ( key , value ) => {
                                        this.props.onChangeForm( 'formUser1' , key , value );
                                    } }
                            />
                        </div>

                        <div>
                            <ComponentOnBoardStep2Form
                                    showTokenType={ this.state.isSoleProprietor == true || this.state.isPartnership == true }
                                    showTransactionMode={ this.state.isPartnership == true || this.state.sCustomize == true }

                                    data={ this.props.data }
                                    form={ this.props.form[ 'formUser2' ] }
                                    result={ this.props.result }

                                    formValidation={
                                        this.props.formValidation.length > 0
                                        ? this.props.formValidation[ 1 ]
                                        : {}
                                    }

                                    onChangeForm={ ( key , value ) => {
                                        this.props.onChangeForm( 'formUser2' , key , value );
                                    } }
                            />
                        </div>

                        <div>
                            <ComponentOnBoardStep2Form
                                    showTokenType={ this.state.isSoleProprietor == true || this.state.isPartnership == true }
                                    showTransactionMode={ this.state.isPartnership == true || this.state.sCustomize == true }

                                    data={ this.props.data }
                                    form={ this.props.form[ 'formUser3' ] }
                                    result={ this.props.result }

                                    formValidation={
                                        this.props.formValidation.length > 0
                                        ? this.props.formValidation[ 2 ]
                                        : {}
                                    }

                                    onChangeForm={ ( key , value ) => {
                                        this.props.onChangeForm( 'formUser3' , key , value );
                                    } }
                            />
                        </div>
                    </SwipeableViews>



                    <div
                            className={ ' companyOnboard-submit-detail-info-step2-padding-30 ' }>

                        <RaisedButton
                                    label={ 'Back' }

                                    onClick={
                                        () => {
                                            this.props.onClickPrev();
                                        }
                                    }
                        />



                        <RaisedButton
                                    label={ 'Next' }
                                    className={ ' companyOnboard-submit-detail-info-step2-float-right ' }

                                    onClick={
                                        () => {
                                            this.props.onClickNext();
                                        }
                                    }
                        />

                    </div>



                </div>


            )
        }
    }



