import React                                                from 'react';
import PropTypes                                            from 'prop-types';


import TextField                                            from 'material-ui/TextField';
import { RadioButton, RadioButtonGroup }                    from 'material-ui/RadioButton';
import { Tabs , Tab }                                       from 'material-ui/Tabs';



import ComponentAbstract                                    from '../../../../_abstract/c.abstract';




import  './s.onboardSubmit.detail.step.3.css';




export default class ComponentOnBoardstep3Form extends ComponentAbstract {


        static propTypes = {
            showTokenType               : PropTypes.bool
            , showTransactionMode       : PropTypes.bool

            , form                      : PropTypes.object
            , data                      : PropTypes.object
            , result                    : PropTypes.object
        }

        static defaultProps = {
            showTokenType               : false
            , showTransactionMode       : false

            , form                      : {}
            , data                      : {}
            , result                    : {}
        }







        constructor( props ) {
            super( props );
        }






        render() {


            return (

                <div className={ ' companyOnboard-submit-detail-info-step3-paddingTop-30 ' } >

                    <div className={ ' companyOnboard-submit-detail-info-step3-flex ' }>
                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-40 ' } >
                            <div    className={ ' companyOnboard-submit-detail-info-step3-title ' } > Name (As per NRIC/Passport) </div>
                        </div>

                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-60 companyOnboard-submit-detail-info-step3-padding-5 ' } >
                            { this.props.form.name }
                        </div>
                    </div>


                    <br/>


                    <div className={ ' companyOnboard-submit-detail-info-step3-flex ' }>
                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-40 ' } >
                            <div    className={ ' companyOnboard-submit-detail-info-step3-title ' } > ID Number </div>
                        </div>

                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-60 companyOnboard-submit-detail-info-step3-padding-5 ' } >
                            { this.props.form.idNumber }
                        </div>
                    </div>


                    <br/>


                    <div className={ ' companyOnboard-submit-detail-info-step3-flex ' }>
                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-40 ' } >
                            <div    className={ ' companyOnboard-submit-detail-info-step3-title ' } > ID Type </div>
                        </div>

                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-60 companyOnboard-submit-detail-info-step3-padding-5 ' } >
                            { this.props.form.idType }
                        </div>
                    </div>


                    <br/>


                    <div className={ ' companyOnboard-submit-detail-info-step3-flex ' }>
                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-40 ' } >
                            <div    className={ ' companyOnboard-submit-detail-info-step3-title ' } > Contact Number (SMS TAC will be sent) </div>
                        </div>

                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-60 companyOnboard-submit-detail-info-step3-padding-5 ' } >
                            { this.props.form.contactNumber }
                        </div>
                    </div>


                    <br/>


                    <div className={ ' companyOnboard-submit-detail-info-step3-flex ' }>
                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-40 ' } >
                            <div    className={ ' companyOnboard-submit-detail-info-step3-title ' } > Contact Type </div>
                        </div>

                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-60 companyOnboard-submit-detail-info-step3-padding-5 ' } >
                            {
                                ( () => {
                                    let label = '';
                                    for ( let i = 0; i < this.props.data.contactType.length; i ++ ) {
                                        let tmpObj = this.props.data.contactType[ i ];
                                        if ( tmpObj[ 'code' ] == this.props.form.contactType ) {
                                            label = tmpObj[ 'label' ];
                                        }
                                    }
                                    return label;
                                } )()
                            }
                        </div>
                    </div>


                    <br/>


                    <div className={ ' companyOnboard-submit-detail-info-step3-flex ' }>
                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-40 ' } >
                            <div    className={ ' companyOnboard-submit-detail-info-step3-title ' } > Email Address </div>
                        </div>

                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-60 companyOnboard-submit-detail-info-step3-padding-5 ' } >
                            { this.props.form.emailAddress }
                        </div>
                    </div>


                    <br/>


                    <div className={ ' companyOnboard-submit-detail-info-step3-flex ' }>
                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-40 ' } >
                            <div    className={ ' companyOnboard-submit-detail-info-step3-title ' } > Email Type </div>
                        </div>

                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-60 companyOnboard-submit-detail-info-step3-padding-5 ' } >
                            {
                                ( () => {
                                    let label = '';
                                    for ( let i = 0; i < this.props.data.emailType.length; i ++ ) {
                                        let tmpObj = this.props.data.emailType[ i ];
                                        if ( tmpObj[ 'code' ] == this.props.form.emailType ) {
                                            label = tmpObj[ 'label' ];
                                        }
                                    }
                                    return label;
                                } )()
                            }
                        </div>
                    </div>


                    <br/>


                    <div className={ ' companyOnboard-submit-detail-info-step3-flex ' }>
                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-40 ' } >
                            <div    className={ ' companyOnboard-submit-detail-info-step3-title ' } > Persona </div>
                        </div>

                        <div className={ ' companyOnboard-submit-detail-info-step3-flex-60 companyOnboard-submit-detail-info-step3-padding-5 ' } >
                            {
                                ( () => {
                                    let label = '';
                                    for ( let i = 0; i < this.props.data.role.length; i ++ ) {
                                        let tmpObj = this.props.data.role[ i ];
                                        if ( tmpObj[ 'code' ] == this.props.form.persona ) {
                                            label = tmpObj[ 'label' ];
                                        }
                                    }
                                    return label;
                                } )()
                            }
                        </div>
                    </div>


                    <br/>


                    {
                        this.props.showTokenType
                        ?
                            (
                                <div className={ ' companyOnboard-submit-detail-info-step3-flex ' }>

                                    <div className={ ' companyOnboard-submit-detail-info-step3-flex-40 ' } >
                                        <div    className={ ' companyOnboard-submit-detail-info-step3-title ' } > Security Token Type </div>
                                    </div>

                                    <div className={ ' companyOnboard-submit-detail-info-step3-flex-60 companyOnboard-submit-detail-info-step3-padding-5 ' } >

                                        <RadioButtonGroup
                                                            name={ 'application' }
                                                            style={ { flex : 6 , marginTop : 30 } }
                                                            defaultSelected={ this.props.form.token }

                                                            value={ this.props.form.token }
                                                            onChange={ ( event , value ) => {
                                                            } } >

                                            {
                                                this.props.data.tokenType.map(
                                                    ( item , index ) => {
                                                        return (
                                                            <RadioButton
                                                                        key={ '_comp_onboard_detail_token_' + index }
                                                                        style={ { width: '70%', fontSize: 12 } }
                                                                        value={ item.code }
                                                                        label={ item.label }
                                                                        disabled
                                                            />
                                                        );
                                                    }
                                                )
                                            }

                                        </RadioButtonGroup>

                                    </div>

                                </div>
                            )
                        :
                            ( null )
                    }



                    <br/>

                    {
                        this.props.showTransactionMode
                        ?
                            (
                                <div className={ ' companyOnboard-submit-detail-info-step3-flex ' }>

                                    <div className={ ' companyOnboard-submit-detail-info-step3-flex-40 ' } >
                                        <div    className={ ' companyOnboard-submit-detail-info-step3-title ' } > Transaction Mode </div>
                                    </div>

                                    <div className={ ' companyOnboard-submit-detail-info-step3-flex-60 companyOnboard-submit-detail-info-step3-padding-5 ' } >

                                        <RadioButtonGroup
                                                            name={ 'status' }
                                                            style={ { flex: 6, marginTop: 30 } }
                                                            defaultSelected={ this.props.form.transactionMode }

                                                            value={ this.props.form.transactionMode }
                                                            onChange={ ( event , value ) => { } } >

                                            {
                                                this.props.data.transactionMode.map(
                                                    ( item , index ) => {
                                                        return (
                                                            <RadioButton
                                                                    key={ '_comp_onboard_detail_transacMode_' + index }
                                                                    style={ { width: '70%', fontSize: 12 } }
                                                                    value={ item.code }
                                                                    label={ item.label }
                                                                    disabled
                                                            />
                                                        );
                                                    }
                                                )
                                            }

                                        </RadioButtonGroup>

                                    </div>

                                </div>
                            )
                        :
                            ( null )
                    }


                </div>


            )
        }
    }



