import React                                                from 'react';
import PropTypes                                            from 'prop-types';




import {
    Grid , Row , Col
}                                                           from 'react-bootstrap';
import TextField                                            from 'material-ui/TextField';
import { RadioButton, RadioButtonGroup }                    from 'material-ui/RadioButton';
import { Tabs , Tab }                                       from 'material-ui/Tabs';
import SelectField                                          from 'material-ui/SelectField';
import MenuItem                                             from 'material-ui/MenuItem';



import { ObjectUtil }		                                from '../../../../../_util/util.object';



import ComponentAbstract                                    from '../../../../_abstract/c.abstract';




import  './s.onboardSubmit.detail.step.2.css';




export default class ComponentOnBoardStep2Form extends ComponentAbstract {


        static propTypes = {
            showTokenType                   : PropTypes.bool
            , showTransactionMode           : PropTypes.bool

            , form                          : PropTypes.object
            , data                          : PropTypes.object
            , result                        : PropTypes.object

            , formValidation                : PropTypes.object

            , onChangeForm                  : PropTypes.func
        }

        static defaultProps = {
            showTokenType                   : false
            , showTransactionMode           : false

            , form                          : {}
            , data                          : {}
            , result                        : {}

            , formValidation                : {}

            , onChangeForm                  : () => {}
        }







        constructor( props ) {
            super( props );
        }






        render() {

            return (

                <div className={ ' companyOnboard-submit-detail-info-step2-paddingTop-30 ' } >

                    <Grid>
                        <Row className="show-grid">
                            <Col md={ 4 } >
                                <div    className={ ' companyOnboard-submit-detail-info-step2-title ' } > Name (As per NRIC/Passport) </div>
                            </Col>

                            <Col md={ 8 } >
                                <TextField
                                            name={ 'field_name_1' }
                                            underlineStyle={ { borderColor: '#000' } }
                                            underlineFocusStyle={ { borderColor: '#FF5252' } }
                                            style={ { width : '100%' } }

                                            errorText={ this.props.formValidation[ 'name' ] }

                                            value={ this.props.form.name }
                                            onChange={ ( evetn , value ) => {
                                                this.props.onChangeForm( 'name' , value );
                                            } }
                                />
                            </Col>
                        </Row>
                    </Grid>

                    <br/>

                    <Grid>
                        <Row className="show-grid">
                            <Col md={ 4 } >
                                <div    className={ ' companyOnboard-submit-detail-info-step2-title ' } > ID Number </div>
                            </Col>

                            <Col md={ 4 } >

                                <TextField
                                            name={ 'field_ic_1' }
                                            underlineStyle={ { borderColor: '#000' } }
                                            underlineFocusStyle={ { borderColor: '#FF5252' } }
                                            style={ { width : '100%' } }

                                            errorText={ this.props.formValidation[ 'idNumber' ] }

                                            value={ this.props.form.idNumber}
                                            onChange={
                                                ( evetn , value ) => {
                                                    this.props.onChangeForm( 'idNumber' , value );
                                                }
                                            }
                                />
                            </Col>
                            <Col md={ 4 } >
                                <SelectField
                                                hintText={ 'ID Type' }
                                                underlineStyle={ { borderColor: '#000' } }
                                                underlineFocusStyle={ { borderColor: '#FF5252' } }

                                                errorText={ this.props.formValidation[ 'idType' ] }

                                                value={ this.props.form.idType }
                                                onChange={
                                                    ( event, index, value ) => {
                                                        this.props.onChangeForm( 'idType' , value );
                                                    }
                                                }
                                                autoWidth={ true } >
                                    {
                                        this.props.data.idType.map(
                                            ( item , index ) => {
                                                return ( <MenuItem  key={ 'comp_onboard_detail_form_user_idType_' + index }   value={ item.code }         primaryText={ item.label } /> );
                                            }
                                        )
                                    }
                                </SelectField>
                            </Col>
                        </Row>

                    </Grid>

                    <br/>

                    <Grid>
                        <Row className="show-grid">
                            <Col md={ 4 } >
                                <div className={ ' companyOnboard-submit-detail-info-step2-title ' } > Contact Number (SMS TAC will be sent) </div>
                            </Col>

                            <Col md={ 4 } style={ { overflow : 'hidden' } } >
                                <div style={ { padding : '2px' } }>
                                    <TextField
                                                    name={ 'field_mobileNo_1' }
                                                    underlineStyle={ { borderColor : '#000' } }
                                                    underlineFocusStyle={ { borderColor : '#FF5252' } }
                                                    type={ 'number' }

                                                    errorText={ this.props.formValidation[ 'contactNumber' ] }

                                                    value={ this.props.form.contactNumber }
                                                    onChange={
                                                        ( evetn , value ) => {
                                                            this.props.onChangeForm( 'contactNumber' , value );
                                                        }
                                                    }
                                    />
                                </div>
                            </Col>
                            <Col md={ 4 } >
                                <SelectField
                                                hintText={ 'Contact Info Type' }
                                                underlineStyle={ { borderColor : '#000' } }
                                                underlineFocusStyle={ { borderColor : '#FF5252' } }

                                                errorText={ this.props.formValidation[ 'contactType' ] }

                                                value={ this.props.form.contactType }
                                                onChange={
                                                    ( event , index , value ) => {
                                                        this.props.onChangeForm( 'contactType' , value );
                                                    }
                                                }
                                                autoWidth={ true } >
                                                {
                                                    this.props.data.contactType.map(
                                                        ( item , index ) => {
                                                            return ( <MenuItem  key={ 'comp_onboard_detail_form_user_contact_' + index }   value={ item.code }         primaryText={ item.label } /> );
                                                        }
                                                    )
                                                }
                                </SelectField>
                            </Col>
                        </Row>
                    </Grid>

                    <br/>


                    <Grid>
                        <Row className="show-grid">
                            <Col md={ 4 } >
                                <div    className={ ' companyOnboard-submit-detail-info-step2-title ' } > Email Address </div>
                            </Col>

                            <Col md={ 4 } >

                                <TextField
                                                name={ 'field_email_1' }
                                                underlineStyle={ { borderColor : '#000' } }
                                                underlineFocusStyle={ { borderColor : '#FF5252' } }
                                                style={ { width : '100%' } }

                                                errorText={ this.props.formValidation[ 'emailAddress' ] }

                                                value={ this.props.form.emailAddress }
                                                onChange={ ( evetn , value ) => {
                                                    this.props.onChangeForm( 'emailAddress' , value );
                                                } }
                                />
                            </Col>
                            <Col md={ 4 } >
                                <SelectField
                                                hintText={ 'Email Type' }
                                                underlineStyle={ { borderColor : '#000' } }
                                                underlineFocusStyle={ { borderColor : '#FF5252' } }

                                                errorText={ this.props.formValidation[ 'emailType' ] }

                                                value={ this.props.form.emailType }
                                                onChange={
                                                    ( event, index, value ) => {
                                                        this.props.onChangeForm( 'emailType' , value );
                                                    }
                                                }
                                                autoWidth={true}>
                                    {
                                        this.props.data.emailType.map(
                                            ( item , index ) => {
                                                return ( <MenuItem  key={ 'comp_onboard_detail_form_user_email_' + index }   value={ item.code }         primaryText={ item.label } /> );
                                            }
                                        )
                                    }
                                </SelectField>
                            </Col>
                        </Row>

                    </Grid>


                    <br/>


                    <Grid>
                        <Row className="show-grid">
                            <Col md={ 4 } >
                                <div    className={ ' companyOnboard-submit-detail-info-step2-title ' } > Persona </div>
                            </Col>

                            <Col md={ 8 } >
                                <SelectField
                                                underlineStyle={ { borderColor: '#000' } }
                                                underlineFocusStyle={ { borderColor: '#FF5252' } }

                                                errorText={ this.props.formValidation[ 'persona' ] }

                                                value={ this.props.form.persona }
                                                onChange={
                                                    ( event, index, value ) => {
                                                        this.props.onChangeForm( 'persona' , value );
                                                    }
                                                }
                                                autoWidth={ true } >

                                    {
                                        this.props.data.role.map(
                                            ( item , index ) => {
                                                return ( <MenuItem  key={ 'comp_onboard_detail_form_user_role_' + index }   value={ item.code }         primaryText={ item.label } /> );
                                            }
                                        )
                                    }
                                </SelectField>
                            </Col>
                        </Row>
                    </Grid>


                    <br/>


                    {
                        this.props.showTokenType
                        ?
                            (
                                <Grid>
                                    <Row className="show-grid">
                                        <Col md={ 4 } >
                                            <div    className={ ' companyOnboard-submit-detail-info-step2-title ' } > Security Token Type </div>
                                        </Col>

                                        <Col md={ 8 } >

                                            {
                                                ObjectUtil.hasProperty( this.props.formValidation , 'token' )
                                                ?
                                                    ( <div style={ { color : 'red' , fontSize : '12px' } }> { this.props.formValidation[ 'token' ] } </div> )
                                                :
                                                    ( null )
                                            }

                                            <RadioButtonGroup
                                                                name={ 'application' }
                                                                style={ { flex : 6 } }

                                                                defaultSelected={ this.props.form.token }

                                                                value={ this.props.form.token }
                                                                onChange={ ( event , value ) => {
                                                                    this.props.onChangeForm( 'token' , value );
                                                                } } >
                                                {
                                                    this.props.data.tokenType.map(
                                                        ( item , index ) => {
                                                            return (
                                                                <RadioButton
                                                                            key={ '_comp_onboard_detail_token_' + index }
                                                                            style={ { width: '70%', fontSize: 12 } }
                                                                            value={ item.code }
                                                                            label={ item.label }
                                                                />
                                                            );
                                                        }
                                                    )
                                                }


                                            </RadioButtonGroup>

                                        </Col>
                                    </Row>
                                </Grid>
                            )
                        :
                            ( null )
                    }


                    <br/>


                    {
                        this.props.showTransactionMode
                        ?
                            (
                                <Grid>
                                    <Row className="show-grid">
                                        <Col md={ 4 } >
                                            <div    className={ ' companyOnboard-submit-detail-info-step2-title ' } > Transaction Mode </div>
                                        </Col>

                                        <Col md={ 8 } >

                                            {
                                                ObjectUtil.hasProperty( this.props.formValidation , 'transactionMode' )
                                                ?
                                                    ( <div style={ { color : 'red' , fontSize : '12px' } }> { this.props.formValidation[ 'transactionMode' ] } </div> )
                                                :
                                                    ( null )
                                            }


                                            <RadioButtonGroup
                                                                name={ 'status' }
                                                                style={ { flex: 6 } }

                                                                defaultSelected={ this.props.form.transactionMode }
                                                                value={ this.props.form.transactionMode }
                                                                onChange={ ( event , value ) => {
                                                                    this.props.onChangeForm( 'transactionMode' , value );
                                                                } } >

                                                {
                                                    this.props.data.transactionMode.map(
                                                        ( item , index ) => {
                                                            return (
                                                                <RadioButton
                                                                        key={ '_comp_onboard_detail_transacMode_' + index }
                                                                        style={ { width: '70%', fontSize: 12 } }
                                                                        value={ item.code }
                                                                        label={ item.label }
                                                                />
                                                            );
                                                        }
                                                    )
                                                }

                                            </RadioButtonGroup>

                                        </Col>
                                    </Row>
                                </Grid>
                            )
                        :
                            ( null )
                    }



                </div>


            )
        }
    }



