
import React                                                from 'react';
import PropTypes                                            from 'prop-types';



import RaisedButton                                         from 'material-ui/RaisedButton';



import ComponentAbstract                                    from '../../../../_abstract/c.abstract';




import './s.onboardSubmit.detail.step.4.css';








export default class ComponentOnBoardStep4 extends ComponentAbstract {


    static propTypes = {
        onClickDone       : PropTypes.func
    }


    static defaultProps = {
        onClickDone       : () => {}
    }










    constructor( props ) {
        super( props );

    }






    render() {
        return (
            <div >


                <h5
                        style={ {
                            marginBottom    : 0
                            , marginTop     : 15
                            , marginBottom  : 30
                        } }>
                    Acknowlegdement
                </h5>

                <hr />


                Confirm





                <div
                        className={ ' companyOnboard-submit-detail-info-step4-padding-30 ' }>

                    <RaisedButton
                                label={ 'Done' }

                                onClick={
                                    () => {
                                        this.props.onClickDone();
                                    }
                                }
                    />


                </div>

            </div>
        )
    }
}



